import React from 'react';
import Sidebar from '../components/Sidebar';
import '../styles/Environments.css';

const Environments: React.FC = () => {
  const environments = [
    {
      id: 1,
      name: 'Biblioteca',
      description: 'Ambiente silencioso para leitura e estudo.',
      icon: '📚',
      capacity: '50 pessoas',
      equipment: 'Wi-Fi, mesas de estudo',
      location: 'Bloco A, 1º andar',
      hours: '08:00 - 18:00',
    },
    {
      id: 2,
      name: 'Salas de Aula',
      description: 'Espaço para aulas e reuniões acadêmicas.',
      icon: '🖊️',
      capacity: '30 pessoas',
      equipment: 'Projetor, quadro branco',
      location: 'Bloco B, 2º andar',
      hours: '08:00 - 22:00',
    },
    {
      id: 3,
      name: 'AlphaLAB',
      description: 'Espaço dedicado à inovação e prototipagem.',
      icon: '💡',
      capacity: '15 pessoas',
      equipment: 'Computadores, impressora 3D',
      location: 'Bloco C, Térreo',
      hours: '09:00 - 17:00',
    },
    {
      id: 4,
      name: 'Auditório',
      description: 'Espaço amplo para palestras e eventos.',
      icon: '🎤',
      capacity: '200 pessoas',
      equipment: 'Microfone, sistema de som, telão',
      location: 'Bloco D, Subsolo',
      hours: '08:00 - 20:00',
    },
  ];

  return (
    <div className="environments-container">
      <Sidebar />
      <div className="content">
        <h1>Ambientes Disponíveis</h1>
        <div className="environments-list">
          {environments.map((env) => (
            <div key={env.id} className="environment-card">
              <div className="header">
                <span className="icon">{env.icon}</span>
                <h2>{env.name}</h2>
              </div>
              <p className="description">{env.description}</p>
              <div className="details">
                <p><strong>Capacidade:</strong> {env.capacity}</p>
                <p><strong>Equipamentos:</strong> {env.equipment}</p>
                <p><strong>Localização:</strong> {env.location}</p>
                <p><strong>Horário de Funcionamento:</strong> {env.hours}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Environments;
